import { AngularFireAuth } from 'angularfire2/auth';
import { UserProvider } from './../providers/user/user';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
//import { HomePage } from '../pages/home/home';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireModule, FirebaseAppConfig } from 'angularfire2';
import { HttpModule } from '@angular/http';
import { AuthProvider } from '../providers/auth/auth';
//import { BaseProvider } from '../providers/base/base';


const firebaseAppConfig: FirebaseAppConfig = {
  apiKey: "AIzaSyDQSg4d1kNlOsoM6XBMLdOq9pMF1PjzJsg",
  authDomain: "app-chat-real-time.firebaseapp.com",
  databaseURL: "https://app-chat-real-time.firebaseio.com",
  projectId: "app-chat-real-time",
  storageBucket: "app-chat-real-time.appspot.com",
  messagingSenderId: "886167588850" 
};



@NgModule({
  declarations: [
    MyApp    
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/home/home.module#SigninPageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signin/signin.module#SigninPageModule', name: 'SigninPage', segment: 'signin', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module#SignupPageModule', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(firebaseAppConfig),
    AngularFireDatabaseModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp//,
   // HomePage,
   // SigninPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}, 
    UserProvider,
    AuthProvider,
    AngularFireAuth//,
    //BaseProvider   
  ]
})
export class AppModule {}



